@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2&display=swap);
* {
  scroll-behavior: smooth;
}
* {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

.App{
  margin-top: 0;
  padding-top: 0;
  min-height: 100vh;
  background-color: rgb(74, 107, 104);
  color: rgb(221, 77, 24);
  padding-bottom: 100px;
  padding-top: 80px;
}

@media(max-width:700px){
  .App{
    padding-top: 70px;
  }
}

.pagetitle{
  cursor: pointer;
  width: 90%;
  position: fixed;
  z-index: 100;
  text-transform: uppercase;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: 'Do Hyeon', sans-serif;
  font-size: 3vw;
  padding: 4px;
  border-radius: 50px;
  color: aqua;
  box-shadow: 2px 3px 5px;
  background-color: rgb(39, 39, 39);
}


@media screen and (max-width: 700px){
  .pagetitle{
      padding-top: 0;
  }
}

.header{
    width: 100%;
    cursor: pointer;
    position: fixed;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    text-transform: uppercase;
    background-color: rgb(51, 51, 51);
    font-family: 'Do Hyeon', sans-serif;
    font-size: 4vw;
    padding-bottom: 2px;
    padding-top: 0;
    margin-top: 0;
    margin-bottom: 0;
    padding-bottom: 0;
    box-shadow: 1px 2px 5px black;
    color: rgb(253, 123, 36);
    z-index: 100;
}

@media screen and (max-width: 700ps){
    .header{
        padding-bottom: 0;
    }
}
.media{
    color: rgb(253, 253, 253);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 200px;
    padding: 5px;
    margin: 5px 0;
    background-color: rgb(39, 39, 39);
    border-radius: 10px;
    position: relative;
    font-family: 'Do Hyeon', sans-serif;
    cursor: pointer;
    box-shadow: 2px 3px 5px aqua;
}

.media:hover{
    background-color: rgb(255, 255, 255);
    color: rgb(50, 51, 50);
}

@media(max-width:550px){
    .media{
        width: 46%;
    }
}

.poster{
    border: 3px solid rgb(28, 167, 192);
    border-radius: 10px;
}

.title{
    width:100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
}

.subtitle{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding-bottom: 3px;
}
.content-landscape{
    object-fit: contain;
    border-radius: 10px;
}

.content-portrait{
    display: none;
    object-fit: contain;
    border-radius: 10px;
}

.tagline{
    padding-bottom: 10px;
    -webkit-align-self: center;
            align-self: center;
    font-family: 'Do Hyeon', sans-serif;
    font-size: 50;
    color: aqua;
}

.contentmodal{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 100%;
    width: 100%;
    overflow-y: scroll;
    scrollbar-width: none;
    font-family: 'Do Hyeon', sans-serif;
}

.contentmodal::-webkit-scrollbar{
    display: none;
}

.content-about{
    padding: 10px;
    width: 95%;
    height: 90%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    font-weight: 300;
}

.content-title{
    height: 12%;
    font-size: 5vw;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: rgb(39, 39, 39);
    padding-top: 20px;
    border-radius: 10px;
    color: aqua;
    box-shadow: 2px 3px 5px;
    position: relative;
}

.content-des{
    display: -webkit-flex;
    display: flex;
    height: 40%;
    overflow-y: scroll;
    padding: 15px;
    border-radius: 20px;
    scrollbar-width: thin;
    box-shadow: inset 0 0 5px black;
    text-align: justify;
    background-color: rgb(39, 39, 39);
    color: aqua;
}

.content-des::-webkit-scrollbar{
    display: none;
}

@media (min-width: 835px){
    .content-landscape{
        display: none;
    }
    .content-portrait{
        display: -webkit-flex;
        display: flex;
        width: 38%;
    }
    .contentmodal{
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-justify-content: space-around;
                justify-content: space-around;
        padding: 10px 0;
    }
    .content-about{
        width: 58%;
        padding: 0;
        height: 100%;
    }
    .content-title{
        font-size: 3.5vw;
    }
    .content-des{
        font-size: 22px;
    }
}

.rating{
    font-weight: bolder;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: rgb(39, 39, 39);
    width: 10%;
    border-radius: 10px;
    padding-bottom: 0;
    margin-bottom: 0;
    -webkit-align-self: center;
            align-self: center;
}

.genres{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.trending{
    padding-top: 65px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.typeof{
    color: black;
    font-weight: bolder;
    font-family:'Times New Roman', Times, serif;
    font-style:oblique;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.movie{
    padding-top: 2px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}



